$bg_color: #fafafa;
$gray_color: #353535;


html,
body {
    margin: 0;
    padding: 0;
}

body {
    font: 16px 'Helvetica Neue', Helvetica, Arial;
    font-weight: 300;
    line-height: 1.5;
    width: auto;
    background: $bg_color;
    color: $gray_color;
    font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 400;
    line-height: 1.2;
    color: #000;
}

h2 {
    margin: 10px 0;
    font-size: 24px;
}

a,
.link {
    color: #9f3c3c;
    font-weight: 500;
    cursor: pointer;
}


a:hover,
.link:hover {
    color: #431919;
    text-decoration: underline;
}

p {
    font-size: 16px;
    line-height: 1.5;
    font-weight: 300;
}

var {
    font-style: normal;
}

header p {
    font-size: 30px;
    line-height: 1.2;
    margin-top: 10px;
}

header nav {
    margin-top: 20px;
    font-size: 20px;
}

header nav a {
    font-weight: 200;
}

header nav a:not(:last-child) {
    margin-right: 5px;
    margin-bottom: 5px;
}

.logo {
    margin-top: 70px;
}

.logo-icon {
    display: none;
}

.row {
    margin-top: 20px;
    margin-bottom: 20px;
}

.screenshot {
    width: 100%;
    height: auto;
    max-width: 558px;
    margin-top: 12px;
    float: right;
}

.tagline2 {
    font-size: 21px;
    font-style: italic;
    margin-top: 40px;
}

.container {
    margin-top: 10px;
    margin-bottom: 20px;
}

.whats-new li {
    position: relative;
    margin: 15px 0;
    list-style: none;
}

.whats-new li:before {
    content: '✔';
    position: absolute;
    left: -20px;
}

.app-lists {
    overflow: hidden;
}

.applist {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 17px;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}

.applist li {
    padding: 10px 0;
    width: 160px;
}

.applist a {
    position: relative; /* popover */
}

.applist-intro {
    margin: 10px 0 10px;
    font-style: italic;
}

.applist .routing::after,
.applist .label,
.legend .label {
    content: 'R';
    position: relative;
    top: -3px;
    font-size: 9px;
    padding: 1px 3px 1px;
    background-color: rgba(0, 0, 0, .1);
    border-radius: 3px;
    font-weight: bold;
    line-height: 13px;
    color: #777;
    vertical-align: middle;
    white-space: nowrap;
    text-shadow: 0 -1px 0 rgba(255, 255, 255, 0.25);
}

.applist .labs a,
.labs-example {
    color: #582C42;
}

.applist a:hover .label {
    text-decoration: none !important;
    color: red;
}

.legend {
    margin-top: 20px;
    margin-left: 0;
    padding: 0;
    list-style-type: none;
}

.legend li {
    margin-bottom: 0.5em;
}

.legend li:last-child {
    margin-bottom: 0;
}

.collapsed {
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.7s ease-out;
}

#news-expander {
    display: none;
}

#news-expander:checked ~ .collapsed {
    max-height: 999px;
    transition: max-height 0.7s ease-in;
}

.credit {
    text-align: center;
}

.credit a {
    margin: 5px;
    white-space: nowrap;
}

.credit a img {
    border-radius: 3px;
    margin-right: 5px;
}

.popover {
    margin: 0;
    cursor: default;
    color: #333;
    white-space: normal;
    z-index: 999;
}

.popover-title {
    padding: 9px 70px 9px 15px;
}

.popover-title a {
    font-size: 13px;
    position: absolute;
    right: 20px;
}

.quote {
    border: none;
    margin: 20px 0 70px 0;
}

.quote p {
    font-size: 17px;
    line-height: 1.3;
    font-style: italic;
}

.quote p:before {
    content: '“';
    font-size: 50px;
    opacity: .15;
    position: absolute;
    top: -20px;
    left: 3px;
}

.quote p:after {
    content: '”';
    font-size: 50px;
    opacity: .15;
    position: absolute;
    bottom: -42px;
    right: 3px;
}

.quote footer {
    position: absolute;
    bottom: -60px;
    left: 12px;
}

.quote footer img {
    border-radius: 3px;
}

.quote footer a {
    margin-left: 5px;
    vertical-align: middle;
}

.speech-bubble {
    position: relative;
    padding: 10px;
    background: rgba(0, 0, 0, .04);
    border-radius: 5px;
}

.speech-bubble:after {
    content: '';
    position: absolute;
    top: 100%;
    left: 20px;
    border: 13px solid transparent;
    border-top-color: rgba(0, 0, 0, .04);
}

.zocial.red {
    background-color: #b12d2b;
}

.zocial.gray {
    background-color: rgba(0, 0, 0, .5);
}

.zocial.ltgray {
    color: inherit;
    text-shadow: none;
}

.zocial.ltgray strong {
    color: #B83F45;
}

.zocial.small {
    font-size: 12px;
    padding: 2px 10px;
}

.zocial,
a.zocial {
    background: #f4f4f4;
    border: 1px solid #777;
    border-color: rgba(0, 0, 0, 0.2);
    border-bottom-color: #333;
    border-bottom-color: rgba(0, 0, 0, 0.4);
    color: white;
    cursor: pointer;
    display: inline-block;
    padding: 6px 10px;
    text-align: center;
    text-decoration: none;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
    border-radius: .3em;
}

@media (max-width: 480px) {
    .credit a {
        display: block;
    }
}

@media (max-width: 770px) {
    h2 {
        margin-top: 20px;
    }

    .row,
    .credit {
        margin-left: 10px;
        margin-right: 10px;
    }

    .logo {
        max-width: 100%;
        margin-top: 0;
    }

    .container {
        margin: auto;
    }
}

@media (min-width: 992px) {
    .logo-icon {
        display: block;
    }
}
